.button {
    color: #fff;
    background-color: #1e3246;
    border: solid #1e3246;
    width: 40%;
    padding: 5px;
    border-radius: 5px;
}

.button:hover {
    background-color: #4b5a69;
}

.button:active {
    background-color: #788291;
}

.button:focus {
    border: solid #dc3223;
}